import React from "react";
import PropTypes from "prop-types";
import { makeStyles } from "@material-ui/core/styles";
import AppBar from "@material-ui/core/AppBar";
import Tabs from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";
import Typography from "@material-ui/core/Typography";
import Box from "@material-ui/core/Box";
import Ouu from "./ouu";
import Kanto from "./kanto";
import Toukai from "./toukai";
import Kinai from "./kinai";
import Sikoku from "./sikoku";
import Kyusyu from "./kyusyu";
import output from "./output";
import outputK from "./outputK";
import outputKI from "./outputKI";
import outputT from "./outputT";
import outputKY from "./outputKY";
import outputS from "./outputS";
import "./styles.css";

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`scrollable-auto-tabpanel-${index}`}
      aria-labelledby={`scrollable-auto-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box p={3}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired
};

function a11yProps(index) {
  return {
    id: `scrollable-auto-tab-${index}`,
    "aria-controls": `scrollable-auto-tabpanel-${index}`
  };
}

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    width: "90%",
    backgroundColor: theme.palette.background.paper
  }
}));

export default function ScrollableTabsButtonAuto() {
  const classes = useStyles();
  const [value, setValue] = React.useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <div className={classes.root}>
      <AppBar className="a" color="default">
        <Tabs
          value={value}
          onChange={handleChange}
          indicatorColor="primary"
          textColor="primary"
          variant="scrollable"
          scrollButtons="auto"
          aria-label="scrollable auto tabs example"
          righted
        >
          <Tab label={output[0]["地区"]} {...a11yProps(0)} />
          <Tab label={outputK[0]["地区"]} {...a11yProps(1)} />
          <Tab label={outputT[0]["地区"]} {...a11yProps(2)} />
          <Tab label={outputKI[0]["地区"]} {...a11yProps(3)} />
          <Tab label={outputS[0]["地区"]} {...a11yProps(4)} />
          <Tab label={outputKY[0]["地区"]} {...a11yProps(5)} />
          <Tab label="その他勢力" {...a11yProps(6)} />
        </Tabs>
      </AppBar>
      <TabPanel value={value} index={0}>
        <ul>
          <Ouu />{" "}
        </ul>
      </TabPanel>
      <TabPanel value={value} index={1}>
        <ul>
          <Kanto />{" "}
        </ul>
      </TabPanel>
      <TabPanel value={value} index={2}>
        <ul>
          <Toukai />{" "}
        </ul>
      </TabPanel>
      <TabPanel value={value} index={3}>
        <ul>
          <Kinai />{" "}
        </ul>
      </TabPanel>
      <TabPanel value={value} index={4}>
        <ul>
          <Sikoku />{" "}
        </ul>
      </TabPanel>
      <TabPanel value={value} index={5}>
        <ul>
          <Kyusyu />{" "}
        </ul>
      </TabPanel>
      <TabPanel value={value} index={6}>
        <a href="https://ja.wikipedia.org/wiki/%E6%88%A6%E5%9B%BD%E5%A4%A7%E5%90%8D#%E3%81%9D%E3%81%AE%E4%BB%96%E3%81%AE%E5%8B%A2%E5%8A%9B">
          詳細{" "}
        </a>
      </TabPanel>
    </div>
  );
}
